<template>
  <el-container>
    <el-aside :width="collapse ? '64px' : '226px'">
      <h3 class="logo-text">{{ title }}</h3>
      <!-- 主导航 -->
      <el-menu
        unique-opened
        :collapse-transition="false"
        :collapse="collapse"
        default-active="2"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#333"
        text-color="#FFF"
        active-text-color="#FFF"
        router
      >
        <el-menu-item index="/home">
          <i class="el-icon-menu"></i>
          <span slot="title">管理中心</span>
        </el-menu-item>

        <el-submenu index="2">
          <template slot="title">
            <i class="el-icon-setting"></i>
            <span>系统设置</span>
          </template>
          <el-menu-item index="/menu">
            <span slot="title">菜单管理</span>
          </el-menu-item>
          <el-menu-item index="/role">
            <span slot="title">角色管理</span>
          </el-menu-item>
          <el-menu-item index="/admin">
            <span slot="title">管理员管理</span>
          </el-menu-item>
        </el-submenu>

        <el-submenu index="3">
          <template slot="title">
            <i class="el-icon-goods"></i>
            <span>商城管理</span>
          </template>
          <el-menu-item index="/cate">
            <span slot="title">分类管理</span>
          </el-menu-item>
          <el-menu-item index="/specs">
            <span slot="title">规格管理</span>
          </el-menu-item>
          <el-menu-item index="/goods">
            <span slot="title">商品管理</span>
          </el-menu-item>
          <el-menu-item index="banner">
            <span slot="title">轮播图管理</span>
          </el-menu-item>
          <el-menu-item index="/member">
            <span slot="title">会员管理</span>
          </el-menu-item>
          <el-menu-item index="seck">
            <span slot="title">活动管理</span>
          </el-menu-item>
        </el-submenu>
      </el-menu>
    </el-aside>
    <el-container>
      <el-header>
        <el-button
          @click="change"
          type="primary"
          icon="el-icon-s-fold"
          size="mini"
        ></el-button>
        <el-breadcrumb separator="/">
          <el-breadcrumb-item>管理中心</el-breadcrumb-item>
          <el-breadcrumb-item>{{ $route.meta.title }}</el-breadcrumb-item>
        </el-breadcrumb>
        <el-button
          icon="el-icon-back"
          @click="$router.back()"
          size="mini"
          circle
        ></el-button>

        <el-dropdown>
          <span class="el-dropdown-link">
            admin
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>个人信息</el-dropdown-item>
            <el-dropdown-item
              ><span @click="fullscreen">全屏预览</span></el-dropdown-item
            >
            <el-dropdown-item>
              <span @click="exit">安全退出</span></el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <el-main>
        <!-- 二级路由占位符 -->
        <router-view />
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {};
</script>

<script>
export default {
  data() {
    return {
      collapse: false,
      title: "小U商城后台",
    };
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    change() {
      this.collapse = !this.collapse;
      this.title = this.collapse ? "U" : "小U商城后台";
    },
    exit() {
      localStorage.removeItem("loginInfo");
      this.$success("退出成功");
      this.$router.push("/login");
    },
    fullscreen() {
      document.documentElement.webkitRequestFullScreen();
    },
  },
};
</script>

<style scoped>
.el-container {
  height: 100vh;
}

/* logo */
.logo-text {
  background: #444;
  color: #fff;
  line-height: 60px;
  font-weight: normal;
  letter-spacing: 3px;
  text-align: center;
}

/* 导航 */
.el-menu {
  height: calc(100vh - 60px);
  border-right: none;
}
/* 导航高亮类名 */
.is-active {
  background: #409eff !important;
}
/* 页面头部 */
.el-header {
  background-color: #fff;
  line-height: 60px;
  display: flex;
  align-items: center;
}
.el-header button {
  height: 26px;
}
.el-breadcrumb {
  margin: 0 20px;
}
.el-dropdown {
  position: absolute;
  right: 20px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
}
</style>


